.team-btn {
  cursor: pointer;
  color: #545454;
  background-color: #0000;
  border: #0000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 12px;
  line-height: 1.14;
  transition: color .5s ease-in-out, transform .5s ease-in-out;
  display: flex;
  position: relative;
}

.team-btn:hover, .team-btn:focus, .team-btn:focus-within {
  color: #ff6b08;
  transform: scale(1.05);
}

.team-btn:after {
  content: "";
  width: 100%;
  height: 2px;
  opacity: .5;
  background-color: #ff6b08;
  transition: opacity .5s ease-in-out;
  animation-name: animateLine;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.team-btn:after:focus, .team-btn:after:hover, .team-btn:after:focus-within {
  opacity: 1;
}

.team-btn:hover:after {
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .team-btn {
    align-items: center;
    margin-top: 0;
    font-size: 16px;
    line-height: 18.75px;
  }
}

@keyframes animateLine {
  0% {
    transform: scaleX(1);
  }

  50% {
    transform: scaleX(.8);
  }

  100% {
    transform: scaleX(1);
  }
}

